.home-body {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    font-family: "Sarabun";
}

.background {
    background-image: url("./background.png");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: top center;
    background-size: cover;
    width: 100%;
    height: 60vh;
    display: flex;
    justify-content: center;
}

.hotel-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 100%;
    padding-top: 0vh !important;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
}

.hotel-container-container {
    margin-top: 8vh;
    padding: 0 8vw;
}

.hotel-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    
    /* height: 30vh; */
    width: 50vh;
    margin: 4vh 4vh 3vh 3vh;
}

.hotel-info:hover {
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.hotel-detail {
    padding: 2px 16px;
    font-size: 2vh;
    margin-bottom: 3px;
}

.hotel-rate {
    padding-top: 1vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 2vh;
}

.hotel-icon {
    height: 35vh;
    width: 50vh;
    object-fit: cover;
}

.comment-slider {
    padding-top: 5vh;
    width: 100%;
    height: 30vh;
}

.search-bar {
    height: 20vh;
}

.split {
    text-align: left;
    width: 100%;
    padding-top: 2vh;
    padding-left: 2vh;
    font-size: 4vh;
    font-weight: 700;
    color: #4c6189;
}

@media only screen and (max-width: 1000px) {
    .hotel-container-container {
        padding: 0;
    }
}

@media only screen and (max-width: 600px) {

    .background {
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-position: top center;
        background-size: cover;
        width: 100%;
        height: 60vh;
    }
}
